*{
    box-sizing: border-box;
}
ul{
    list-style:none;
    padding : 0;
}
body{
    margin : 0;
    padding : 0;
    width:100%;
    
}
.module_index table tr{
    display : flex;
    align-items: center;
    // border : 1px solid;
}
.module_index table td:nth-child(1){
    // border-right:1px solid;
    padding : 5px;
}
header{
    
     height : 100px;
    
    padding-left : 20px;
    background-color: black;
    color : white;
    position:fixed;
    left:0;
    right:0;
    top:0;
    margin : auto;
    width:100%;
    z-index: 999;
    
    .copyright{

        position:absolute;
        right:10px;
        bottom : 10px;
        color:white;
        font-size:.7rem;
        text-align:right;
        line-height  :1.5;
    }
}
main{
    padding : 20px;
    height : 100%;
    overflow: auto;
    position:relative;
    margin-top : 100px;
    
    
}
footer{
    width : 100%;
    height : 35px;
    padding-left : 20px;
    display:flex;
    justify-content: left;
    align-items: center;
    background-color: lightgray;
    position:fixed;
    left:0px;
    top:100px;
    z-index: 888;
    button{
        margin-right:5px;
    }
  
  
}

.module_index button{
    font-size : .8rem;
    margin-right:3px;
}

.module-table  tr{
   border : 1px solid;
}
.module-table td:nth-child(1){
    border-right : 1px solid;
}




.module-table  .module-group{
    width:100% !important;
    text-align: center;
}

.filter-group{

    display:flex;
    align-items: center;
    height : 30px;
    
}
.filter-group input[type="checkbox"]{
    margin-right : 3px;
    padding : 0;
}
.filter-group label{
    margin-right : 15px;
    font-size : .8rem;
    display:flex;
    
}

.summary-note button{
    background-color: transparent;
    border : 0;
    outline : none;
    cursor: pointer;
    font-size : .6rem;

}


.result-table  tr{
    border : 0;
}
.result-table  td,
.result-table  th
{
    border : 1px solid;
}
  

/* Mobile-specific styles */
@media screen and (max-width: 768px) {
    header {
        height: 80px;
        padding-left: 10px;
        h2{
            font-size:1rem;
        }
        font-size : .9rem;
    }
    header .copyright {
        display:none;
    }
    main {
        padding: 10px;
        margin-top: 90px;
    }
    footer {
        top:80px;
        height : 35px;
        font-size : .7rem;
    }

    .module-table  tr{
        border : 1px solid;
     }
     .module-table td,
     .module-table th{
         border : 1px solid;
     }

     
    .module_index button {
        font-size: 0.7rem;
    }
 
    .module-table tbody tr {
        position:relative;
        border : 1px solid;
    }
    .module-table th,td{
        border : 0px !important;
       
    }
    .module-table tbody tr th:nth-child(1),
    .module-table tbody tr td:nth-child(1){
      
        position:relative;
        left:0;
        width:30px;
    }  


    .module-table tbody tr th:nth-child(2),
    .module-table tbody tr td:nth-child(2){

        max-width:calc(100% - 30px - 50px);
        font-size : .4rem !important;
        display:flex;
        justify-content: center;
        align-items: center;
        position:absolute;
        left:30px;
    }
    .module-table tbody tr th:nth-child(3),
    .module-table tbody tr td:nth-child(3){   
       
        max-width: 50px;
        min-width: 50px;
        position:absolute;
        right:0;
        font-size : .4rem !important;
    }

    .summary-note button{
        background-color: transparent;
        border : 0;
        outline : none;
        cursor: pointer;
        font-size : .4rem !important; 
    
    }

    // RESULT TABLE
    .result-table tbody tr
    {
        position:relative;
        border : 1px solid !important;
    }

    .result-table tbody tr th:nth-child(1),
    .result-table tbody tr td:nth-child(1){
      
        position:relative;
        left:0;
        width:20px;
        font-size : .5rem;
    }  


    .result-table tbody tr th:nth-child(2),
    .result-table tbody tr td:nth-child(2){

        max-width: 180px !important;
        min-width: 180px !important;
        font-size : .4rem !important;
        display:flex;
        justify-content: center;
        align-items: center;
        position:relative;
        left:0px;
        
    }
    .result-table tbody tr th:nth-child(3),
    .result-table tbody tr td:nth-child(3){   
       
        max-width: 50px;
        min-width: 50px;
        position:relative;
        font-size : .4rem !important;
    }
    .result-table tbody tr th:nth-child(4),
    .result-table tbody tr td:nth-child(4){   
       
        max-width: 50px;
        min-width: 50px;
        position:absolute;
        right:0;
        font-size : .4rem !important;
    }

}

